.bt-video-container {
    position: relative;
    aspect-ratio: 16 / 9;
    width: 100%;
    margin: 0 0 50px;

    a {
        position: relative;
        display: block;
        height: 100%;
        width: 100%;
        background-size: cover !important;

        &::before {
            position: absolute;
            content: "";
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            background: rgb(0, 0, 0);
            opacity: 0.5;
            transition: opacity 0.2s ease;
        }

        &::after {
            position: absolute;
            content: "\f04b";
            font-family: "Line Awesome Free";
            font-weight: 900;
            width: 100px;
            height: 100px;
            font-size: 100px;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            pointer-events: none;
            color: #fff;
            filter: drop-shadow(0, 0, 5px, rgba(0, 0, 0, 0.2));
            opacity: 1;
            transition: opacity 0.2s ease;
        }

        &:hover {
            &::before {
                opacity: 0.2;
            }

            &::after {
                opacity: 0.75;
                transition: opacity 0.2s ease;
            }
        }
    }

    .video {
        aspect-ratio: 16 / 9;
        width: 100%;
    }
}
